'use client';
import { useState, useEffect } from 'react';
import styles from './foro.module.css';

interface Debate {
  id: number;
  titulo: string;
  descripcion: string;
  autor: string;
  comentarios: { id: number; texto: string; autor: string; fecha: string }[];
}

export default function ForoPage() {
  const [debates, setDebates] = useState<Debate[]>([]);
  const [nuevoTitulo, setNuevoTitulo] = useState('');
  const [nuevaDescripcion, setNuevaDescripcion] = useState('');
  const [comentario, setComentario] = useState('');
  const [debateSeleccionado, setDebateSeleccionado] = useState<Debate | null>(null);

  // Simulación de carga de datos
  useEffect(() => {
    setDebates([
      {
        id: 1,
        titulo: 'El impacto de la IA en la educación moderna',
        descripcion: '¿La inteligencia artificial sustituirá a los maestros o los ayudará?',
        autor: 'Goliva',
        comentarios: [
          { id: 1, texto: 'Excelente tema, muy relevante hoy en día.', autor: 'María', fecha: '2025-11-12' },
        ],
      },
      {
        id: 2,
        titulo: 'Ética en el uso de datos personales',
        descripcion: 'Cada vez se recopilan más datos, ¿cómo proteger nuestra privacidad?',
        autor: 'Carlos',
        comentarios: [],
      },
    ]);
  }, []);

  const crearDebate = () => {
    if (nuevoTitulo.trim() && nuevaDescripcion.trim()) {
      const nuevoDebate: Debate = {
        id: debates.length + 1,
        titulo: nuevoTitulo,
        descripcion: nuevaDescripcion,
        autor: 'Goliva',
        comentarios: [],
      };
      setDebates([...debates, nuevoDebate]);
      setNuevoTitulo('');
      setNuevaDescripcion('');
      alert('Nuevo debate creado');
    }
  };

  const agregarComentario = () => {
    if (comentario.trim() && debateSeleccionado) {
      const nuevoComentario = {
        id: Date.now(),
        texto: comentario,
        autor: 'Goliva',
        fecha: new Date().toLocaleDateString(),
      };

      const actualizados = debates.map((d) =>
        d.id === debateSeleccionado.id
          ? { ...d, comentarios: [...d.comentarios, nuevoComentario] }
          : d
      );

      setDebates(actualizados);
      setComentario('');
    }
  };

  return (
    <>
      {/* HEADER */}
      <header className={styles.header}>
        <h1>Foro de Debates</h1>
        <div className={styles.headerButtons}>
          <button
            className={styles.backButton}
            onClick={() => (window.location.href = '/')}
          >
            Volver al inicio
          </button>
          <button
            className={styles.newDebateButton}
            onClick={() => setDebateSeleccionado(null)}
          >
            Nuevo Debate
          </button>
        </div>
      </header>

      <div className={styles.container}>
        {!debateSeleccionado ? (
          <>
            {/* FORMULARIO NUEVO DEBATE */}
            <h2 className={styles.subtitulo}>Crear un nuevo debate</h2>
            <input
              type="text"
              placeholder="Título del debate"
              value={nuevoTitulo}
              onChange={(e) => setNuevoTitulo(e.target.value)}
              className={styles.input}
            />
            <textarea
              placeholder="Descripción del debate"
              value={nuevaDescripcion}
              onChange={(e) => setNuevaDescripcion(e.target.value)}
              className={styles.textareaComentario}
            />
            <button onClick={crearDebate} className={styles.buttonComentario}>
              Publicar debate
            </button>

            {/* LISTA DE DEBATES */}
            <ul className={styles.debateList}>
              {debates.map((debate) => (
                <li
                  key={debate.id}
                  className={styles.debateItem}
                  onClick={() => setDebateSeleccionado(debate)}
                >
                  <h3 className={styles.debateTitle}>{debate.titulo}</h3>
                  <p className={styles.debateDescription}>{debate.descripcion}</p>
                  <p className={styles.debateAuthor}>Por {debate.autor}</p>
                </li>
              ))}
            </ul>
          </>
        ) : (
          <>
            {/* DETALLES DEL DEBATE */}
            <h2 className={styles.debateTitle}>{debateSeleccionado.titulo}</h2>
            <p className={styles.debateDescription}>{debateSeleccionado.descripcion}</p>
            <p className={styles.debateAuthor}>Creado por {debateSeleccionado.autor}</p>

            {/* COMENTARIOS */}
            <div className={styles.comentariosList}>
              <h3>Comentarios</h3>
              {debateSeleccionado.comentarios.map((c) => (
                <div key={c.id} className={styles.comentario}>
                  <strong>{c.autor}</strong>
                  <small>{c.fecha}</small>
                  <p>{c.texto}</p>
                </div>
              ))}
            </div>

            <textarea
              className={styles.textareaComentario}
              placeholder="Escribe un comentario..."
              value={comentario}
              onChange={(e) => setComentario(e.target.value)}
            />

            <div className={styles.comentarioButtons}>
              <button className={styles.buttonComentario} onClick={agregarComentario}>
                Enviar comentario
              </button>
              <button
                className={styles.backButton}
                onClick={() => setDebateSeleccionado(null)}
              >
                Volver a debates
              </button>
            </div>
          </>
        )}
      </div>
    </>
  );
}


